@import url(https://fonts.googleapis.com/css2?family=Dela+Gothic+One&family=Oswald:wght@200;300;400&family=Poppins:wght@400;900&family=Roboto+Condensed:wght@300;400;700&family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav{   
    background-color: rgba(128, 128, 128, 0);
    padding:40px 110.5px;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:space-between ;
            justify-content:space-between ;
    font-family: 'Roboto', sans-serif;
    -webkit-align-items: center;
            align-items: center;
    transition:0.5s ease;
}
.active{
    background-color:white;
    padding:22px 110.5px;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:space-evenly;
            justify-content:space-evenly;
    font-family: 'Roboto', sans-serif;
    -webkit-align-items: center;
            align-items: center;
    color: rgb(109, 108, 108);
    position: fixed;
    top:0;
    right: 0;
    left:0;
    transition:0.5s ease;
}
.activeLogo{
    color:rgb(0, 0, 0);
    cursor:pointer; 
    display: -webkit-flex; 
    display: flex;
    -webkit-align-self: center;
            align-self: center;
    font-size: 14px;
    font-weight:bold;
}
nav ul{   
    display:-webkit-flex;   
    display:flex;
    list-style: none;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 0;
    margin-top: 0;
}
nav li{
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 0;
    margin-left: 51px;
    text-decoration: none;
    
    
}
li:hover{
    cursor: pointer;
    
}
.logo{
    color:white;
    cursor:pointer; 
    display: -webkit-flex; 
    display: flex;
    -webkit-align-self: center;
            align-self: center;
    font-size: 14px;
    font-weight:bold;
}
.nav-btn{
    background-color: rgb(245, 221, 3);
    padding:0.75rem 1.4rem;
    border-radius: 4px;
    color:black;   
}
nav li a{
    text-decoration: none;
    color:rgb(172, 169, 169);
}
a:hover{
    color:white;
}
.top-img{
    width:100vw;
}



.vision-text{
 margin:auto auto;
 -webkit-align-self: center;
         align-self: center;
  
    width:85%;
}
.content{
    color: rgba(128, 126, 126, 0.815);
    font-family: 'Roboto Condensed', sans-serif;
}
hr{
    height:10px;
    border:solid rgba(161, 160, 160, 0.438) 2px;
    border-bottom: none;
    border-right: none;
    border-left:none;
}

.division{
    
    margin:100px auto 60px auto;
    width:85%;
}

.team-card{
    display: grid;
    grid-template-columns: 23% 23% 23% 23%;
    grid-column-gap: 3%;
    grid-row-gap:20px;
    width: 85%;
    margin:20px auto 40px auto;
    
}




.card{
    font-family: 'Roboto Condensed', sans-serif;
    text-align: center;
}
.card img{
    width:100%;
}
.card h1{
    font-size: 20px;
}
.card h2{
    font-size: 15px;
    color:grey;
    font-family: sans-serif;
    font-weight: lighter;
}


.subject{
    width:85%;
    margin:auto;
    
}
.subject h1{
    color:grey;
    font-size: 25px;
    font-family: sans-serif;
    font-weight: lighter;
}


.card2{
    font-family: 'Roboto Condensed', sans-serif;
    text-align: center;
    margin-bottom: 25px;
}
.card2 img{
    width:100%;
}
.card2 h1{
    font-size: 20px;
}
.card2 a{
    text-decoration: none;
    color:black;
}
.btn{
    background-color:rgba(255, 255, 255, 0);
    border-radius: 3px;
    border:solid black 1px;
    padding:2px 10px;
    margin:0 15px;
    color: black;
    font-size: 20px;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    width:40px;
}
.btn:hover{
    background-color: rgb(54, 99, 223);
    border:none;
}

form h1{
    font-size: 16px;
    color:grey;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: lighter;
}
.contact{
    display:grid;
    grid-template-columns: 35% 50%;
    grid-column-gap: 15%;
    -webkit-column-gap: 15%;
            column-gap: 15%;
    width:85%;
    margin:auto auto;
}
form{
    border-radius: 4px;
    padding: 3rem 2.25rem;
    background:rgb(255, 255, 255);
    border-radius: 2px;
    border:solid rgba(128, 128, 128, 0.171) 0.2px;
    box-shadow: 0px 0px 10px rgba(218, 218, 218, 0.329);
}
input{
    width:100%;
    height:25px;
    margin:10px auto 30px auto;
    padding:5px;
    border:solid rgba(128, 128, 128, 0.171) 0.2px;
    background-color: rgba(239, 239, 243, 0.582);
}
label{
    font-size: 12px;
    color:rgb(153, 153, 153);
}
textarea{
    width:100%;
    height:150px;
    overflow:auto;
    resize:vertical;
    border:solid rgba(128, 128, 128, 0.171) 0.2px;
    background-color: rgba(239, 239, 243, 0.582);
    margin-bottom: 50px;
}
.details{
    margin:10px 0px;   
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 30%;
    -webkit-column-gap: 30%;
            column-gap: 30%;
    grid-template-rows:auto auto;
   
}
button{
    padding:9px 15px;
    background-color:#4e7bee;
    border-radius: 3px;
    font-weight: bold;
    font-family: 'Roboto Condensed', sans-serif;
    color:white;
    border:0px;
    font-size:17px;

}
.details span{
    font-size: 17px;
    color:#4e7bee;
    font-weight: bold;
    font-family: 'Roboto Condensed', sans-serif;
}
.details p{
    color:rgba(128, 128, 128, 0.822);
    font-family: Roboto Condensed,sans-serif!important;
    font-size: 18px;
}


img{
    width:40%;
    padding-right: 20px;
    display: inline;
}

.footer {
    padding: 20vh 7.2vw 2rem;
    display: grid;
    grid-template-columns: 18% 7% 7% 15% 33%;
    grid-column-gap: 7vw;
    -webkit-column-gap: 7vw;
            column-gap: 7vw;
    word-wrap: break-word;
}

.footer p{
    color:rgba(128, 128, 128, 0.719);
    font-size: 17px;
}
.footer a{
    text-decoration: none;
    font-size: 17px;
}
.footer h1{
    font-size: 18px;
    color:grey;
    font-weight:300;
    margin-bottom: 0px;

}
.footer h2{
    margin-bottom:40px;
}
.copyright{
    color:grey;
    font-size: 14px;
    text-align: center;
}
.navbar{
    position:absolute;
    top:0;
    right:0;
    left:0;
}

